import type { IHiddenItems } from "@helpers/types"
import type { IInputFields } from "@interfaces/input-field-interface"
import { values } from "@utils/utils"
import { dp } from "@views/ts/product/variables"

export function isContainerHidden (visibility: IHiddenItems) {
  return isFieldHidden(visibility, 0)
}

export function isFieldHidden (visibility: IHiddenItems, id_field: number) {
  return visibility.fields?.includes(id_field)
}

export function isOptionHidden (visibility: IHiddenItems, id_option: number) {
  return visibility.options?.includes(id_option)
}

export function isGroupHidden (visibility: IHiddenItems, id_group: number) {
  return visibility.groups?.includes(id_group)
}

export function isStepHidden (visibility: IHiddenItems, id_step: number) {
  return visibility.steps?.includes(id_step)
}

export function isGroupFieldsHidden (input_fields: IInputFields, id_group: number, id_step: number) {
  let group_fields = dp.grouped_fields[id_group]
  if (group_fields) {
    const has_visible_field = values(group_fields.fields).find(field => {
      const is_default_step = id_step === 0
      const field_in_step = id_group !== 0 || field.id_step === id_step
      return input_fields?.[field.name]?.visible &&
        (is_default_step || field_in_step)
    })
    return !has_visible_field
  }
  return true
}
