<script lang="ts">import StepsButtons from "@app/Steps/StepsButtons.svelte";
import StepsLabels from "@app/Steps/StepsLabels.svelte";
import { forEach } from "@utils/foreach";
import { reorder } from "@utils/reorder";
import { dp_trans } from "@utils/trans-helper";
import { addHelpers } from "@views/ts/product/utils/fields";
import { dp, dp_adapter, dp_calc, dp_id_module, dp_step, dp_ui, fields, input_fields, prestashop } from "@views/ts/product/variables";
import { onMount } from "svelte";
import { fade } from "svelte/transition";
import AdminSaveButton from "./AdminSaveButton.svelte";
import { calculate, recalculate, updateQuantity } from "./calculator";
import CartButton from "./CartButton.svelte";
import Messages from "./Components/Messages.svelte";
import StockWarning from "./Components/StockWarning.svelte";
import Weight from "./Components/Weight.svelte";
import FieldGroup from "./FieldGroup.svelte";
import InfoPopup from "./InfoPopup.svelte";
import { showDebugInfo } from "./utils/debug";
import { stopEvent, updateBodyClasses } from "./utils/dom";
import { infoTooltip } from "./utils/info-tooltip";
import { updatePrices } from "./utils/prices";
import { getConvertedValues } from "./utils/sync";
import { isContainerHidden } from "./utils/visibility";
addHelpers();
$: updatePrices($dp_calc);
$: dp.main_config.debug_mode && showDebugInfo($dp_calc);
$: updateBodyClasses($dp_calc.met_conditions);
let hidden = true;
$: hidden = isContainerHidden($dp_calc.visibility) && !dp.error;
prestashop.on("updatedProduct", (params) => {
    dp.id_attribute = +params.id_product_attribute;
    updateQuantity();
    updatePrices($dp_calc);
    if (!dp_adapter.isLocked("recalc")) {
        calculate($input_fields);
    }
});
function detectSubmit(e) {
    if (e.target.tagName.toLowerCase() === "textarea") {
        return;
    }
    if (e.key === "Enter") {
        stopEvent(e);
    }
}
dp_adapter.registerSyncCallback(dp_id_module, (name, value) => {
    const values = getConvertedValues(name, value);
    forEach(values, (value, field_name) => {
        if ($input_fields[field_name]) {
            $input_fields[field_name].value = value;
        }
        $input_fields["changed"].value = field_name;
    });
});
onMount(() => {
    if (dp.calculation) {
        updatePrices(dp.calculation);
        if (dp.main_config.debug_mode) {
            showDebugInfo(dp.calculation);
        }
        updateBodyClasses(dp.calculation.met_conditions);
    }
    recalculate($input_fields);
});
</script>

<div class="dp_container" class:dp_hidden={hidden} on:keypress={detectSubmit}>
  {#if dp.error && dp.is_admin && $dp_calc}
    <div class="alert alert-info">
      {dp_trans('An error prevented the dynamic product module from displaying the fields')}
    </div>
    <div class="alert alert-danger">{dp.error}</div>
  {:else}
    {#if $dp_ui.loading}
      <img width="36" class="loader"
           src="{dp.uri}views/img/icons/loader.svg"
           alt="{dp_trans('Loading...')}"
           transition:fade>
    {/if}
    <div class="dp-controls-container" class:dp-blur={$dp_ui.blur_ui}>
      <section class="dp_step" class:dp_step_styled={!!$dp_step.current_step}>
        <StepsLabels/>
        {#each reorder($fields) as field_group (field_group.id)}
          <FieldGroup {field_group}/>
        {/each}
        <StepsButtons/>
      </section>

      <InfoPopup/>

      {#if dp.config.display_weight}
        <Weight/>
      {/if}

      {#if $dp_calc.oos}
        <StockWarning/>
      {/if}
    </div>

    <Messages/>

    {#if dp.main_config.debug_mode && dp.is_admin}
      <div class="alert alert-warning"
           use:infoTooltip
           title={dp_trans('You can disable it in the main configuration page of the module')}>
      <span class="dp_tooltip_btn">
        <i class="material-icons">info</i>
      </span>
        {dp_trans('Dynamic Product: Debug mode enabled')}
      </div>
    {/if}

    <CartButton/>
    {#if dp.is_admin_edit}
      <AdminSaveButton/>
    {/if}
  {/if}
</div>

<style>
  .dp_container {
    position: relative;
  }

  .loader {
    z-index: 1;
    position: fixed;
    right: 5px;
    top: 5px;
  }

  .dp-controls-container {
    opacity: 1;
  }

  .dp-blur {
    transition: opacity ease-in-out .5s;
    opacity: 0.3;
  }

</style>
