import { Adapter } from "@views/ts/adapter/Adapter"
import * as loadcss from "loadcss"
import "../plugins/jquery.ui.touch-punch.min"

import Product from "./Product.svelte"
import { moveContainer } from "./utils/dom"

declare const dp_id_module: number
const adapter = new Adapter()
adapter.registerModule(dp_id_module, {
  customization_callback: null,
  sync_callback         : null,
  response_callback     : null,
})

const dp_container = document.getElementById("dp_product")
if (dp_container && +dp_container.dataset.move_container) {
  moveContainer(dp_container)
}


new Product({
  target: dp_container
})

loadcss("https://fonts.googleapis.com/icon?family=Material+Icons")
