import * as loadcss from "loadcss"

export function picker (node: HTMLElement, { default_color, color }) {

  let pickr
  let currentColor = color

  async function init () {
    loadcss("https://cdn.jsdelivr.net/npm/@simonwep/pickr/dist/themes/nano.min.css")
    const Pickr = await import("@simonwep/pickr/dist/pickr.es5.min")

    window.Pickr = Pickr

    pickr = Pickr.create({
      el     : node,
      theme  : "nano",
      default: default_color || "#2f83a8",

      useAsButton: true,

      swatches: [
        "rgba(244, 67, 54, 1)",
        "rgba(233, 30, 99, 1)",
        "rgba(156, 39, 176, 1)",
        "rgba(103, 58, 183, 1)",
        "rgba(63, 81, 181, 1)",
        "rgba(33, 150, 243, 1)",
        "rgba(3, 169, 244, 1)",
        "rgba(0, 188, 212, 1)",
        "rgba(0, 150, 136, 1)",
        "rgba(76, 175, 80, 1)",
        "rgba(139, 195, 74, 1)",
        "rgba(205, 220, 57, 1)",
        "rgba(255, 235, 59, 1)",
        "rgba(255, 193, 7, 1)"
      ],

      components: {
        preview    : true,
        hue        : true,
        interaction: {
          hex  : true,
          input: true,
        }
      },

    })

    pickr.on("change", (color) => {
      const event = new CustomEvent("change", { detail: { color: color.toHEXA().toString() } })
      node.dispatchEvent(event)
    })

    pickr.on("changestop", (_, pickr) => {
      const event = new CustomEvent("changestop", { detail: { color: pickr._color.toHEXA().toString() } })
      node.dispatchEvent(event)
    })

    pickr.on("swatchselect", (color) => {
      const event = new CustomEvent("swatchselect", { detail: { color: color.toHEXA().toString() } })
      node.dispatchEvent(event)
      setTimeout(() => pickr.hide(), 200)
    })

    pickr.on("show", () => {
      if (currentColor) {
        pickr.setColor(currentColor)
      }
    })
  }

  init()

  return {
    update ({ color }) {
      if (color) {
        currentColor = color
      }
    },
    destory () {
      if (pickr) {
        pickr.destroy()
      }
    }
  }
}
