import { goToField } from "@app/utils/fields";
import { FieldTypes } from "@data/field-types";
import { forEach } from "@utils/foreach";
import { values } from "@utils/utils";
import { dp_step, dp_val, dp_validation, input_fields } from "@views/ts/product/variables";
import { get } from "svelte/store";

export function validateField (field_name: string) {
  if (dp_validation[field_name]) {
    let validate_func = dp_validation[field_name];
    const result = validate_func.apply(null);
    if (result === true) {
      dp_val.resetError(field_name);
    } else {
      dp_val.setError(field_name, result);
      return false;
    }
  }
  return true;
}

export function validateFields () {
  let valid = true;

  let inputFields = get(input_fields);
  let visibleError = values(inputFields)
    .find(input_field => input_field.type === FieldTypes._DP_ERROR_ && input_field.visible);
  if (visibleError) {
    goToField(visibleError.name);
    return false;
  }

  forEach(dp_validation, (validate_func: () => {}, field_name: string) => {
    const result = validate_func.apply(null);
    if (result === true) {
      dp_val.resetError(field_name);
    } else {
      dp_val.setError(field_name, result);
      valid = false;
    }
  });

  let errorFieldName = Object.keys(get(dp_val))[0];
  if (errorFieldName) {
    goToField(errorFieldName);
  }

  return valid;
}

export function validateSteps () {
  return !get(dp_step).next_step;
}
