<script lang="ts">import ShortDescription from "@app/Fields/Components/ShortDescription.svelte";
import { execCustomFunction, getOptionsTotal } from "@app/utils/fields";
import { validateField } from "@app/utils/validator";
import { formatMessage } from "@utils/message";
import { reorder } from "@utils/reorder";
import { values } from "@utils/utils";
import { abortCalculations, recalculate } from "../calculator";
import { castNumber } from "../utils/math";
import { dp_message, dp_validation, input_fields } from "../variables";
import Thumbnail from "./Thumbnails/Thumbnail.svelte";
export let field;
const max_size = field.settings.max_size || 26;
let size = `${max_size}px`;
$: value = $input_fields[field.name].value;
$: updateSelectedOption(value);
function triggerUpdate() {
    $input_fields["changed"].value = field.name;
    abortCalculations();
    execCustomFunction(field.name, $input_fields[field.name]);
    $input_fields[field.name].options = JSON.stringify($input_fields[field.name].selected_options);
    if (validateField(field.name)) {
        recalculate($input_fields);
    }
}
function selectOption(option) {
    const selected = $input_fields[field.name].selected_options.map(Number).includes(option.id);
    if (selected) {
        if ($input_fields[field.name].selected_options.length > 1) {
            $input_fields[field.name].selected_options = $input_fields[field.name].selected_options.filter((id) => +id !== +option.id);
            $input_fields[field.name].value = getOptionsTotal(field.options, $input_fields[field.name].selected_options, field.settings.multiselect);
            triggerUpdate();
        }
        else if (!field.settings.required) {
            $input_fields[field.name].selected_options = [];
            $input_fields[field.name].value = 0;
            triggerUpdate();
        }
    }
    else {
        if (field.settings.multiselect) {
            $input_fields[field.name].selected_options.push(+option.id);
            $input_fields[field.name].value = getOptionsTotal(field.options, $input_fields[field.name].selected_options, field.settings.multiselect);
        }
        else {
            $input_fields[field.name].selected_options = [+option.id];
            $input_fields[field.name].value = getOptionsTotal(field.options, $input_fields[field.name].selected_options, field.settings.multiselect);
        }
        triggerUpdate();
    }
}
function updateSelectedOption(value) {
    const current_value = getOptionsTotal(field.options, $input_fields[field.name].selected_options, field.settings.multiselect);
    if (current_value !== castNumber(value)) {
        let option = values(field.options).find((option) => castNumber(option.value) === castNumber(value));
        if (option) {
            $input_fields[field.name].selected_options = [option.id];
        }
    }
}
$: selected_options = $input_fields[field.name].selected_options;
$: currentImageUrl = getCurrentUrl(selected_options);
$: syncProductImage(currentImageUrl);
function getCurrentUrl(options) {
    if (options.length !== 1) {
        return null;
    }
    const option = field.options[options[0]];
    if (option) {
        return option.image_url;
    }
    return null;
}
function syncProductImage(image_url) {
    if (field.name !== "preview") {
        return;
    }
    if (image_url) {
        const selector = window.dp_product_image_selector || ".product-cover img";
        jQuery(selector).prop("src", image_url);
    }
}
dp_validation[field.name] = () => {
    if (!$input_fields[field.name].visible) {
        return true;
    }
    const selected_options = $input_fields[field.name].selected_options;
    if (field.settings.required && !selected_options.length) {
        return formatMessage(dp_message.select, {
            label: field.label,
        });
    }
    return true;
};
</script>

{#if field.label}
  <label class="attribute_label">
    {field.label}
  </label>
{/if}

<slot name="tooltip"/>

<div class="dp_input_container" id={`dp_${field.name}`}>
  <ul>
    {#each reorder(field.options) as option}
      <Thumbnail {field} {option} {size} {selectOption}/>
    {/each}
  </ul>

  <ShortDescription {field}/>

</div>

<style>
  ul {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
  }

</style>
