<script lang="ts">export let field;
export let option;
export let size = "auto";
export let selectOption;
import OptionValue from "@app/Components/OptionValue.svelte";
import { openGallery } from "@app/utils/gallery";
import { isOptionHidden } from "@app/utils/visibility";
import { dp_calc, input_fields } from "@app/variables";
import { dp_trans } from "@utils/trans-helper";
let selected;
$: selected = $input_fields[field.name].selected_options.map(Number).includes(option.id);
let has_image = !!option.thumb_url;
</script>

{#if !isOptionHidden($dp_calc.visibility, option.id)}
  <li data-id={option.id}
      title={option.label + (option.displayed_value ? ` (+${option.displayed_value}${field.settings.price_unit})` : '')}
      class:dp_selected={selected}
  >
    <button class="dp_btn dp_thumb_btn" on:click|preventDefault={() => selectOption(option)}>
      <div class="dp_thumb"
           style="{option.color && `background-color: ${!has_image ? option.color: 'transparent'}`}; width: {has_image ? 'auto': size}; height: {has_image ? 'auto': size}"
      >
        {#if has_image}
          <img src={option.thumb_url} alt="{option.label}" height={size}>
          {#if +field.settings.max_size >= 40}
            <button
              on:click|stopPropagation|preventDefault={() => openGallery({
              options: field.options,
              settings:field.settings,
              id_current: option.id,
              selected: $input_fields[field.name].selected_options,
              selectFn: selectOption
            })}
              tabindex="-1"
              class="dp_btn dp-btn-round dp-btn-small dp-zoom"
              title={dp_trans('Enlarge image')}
            >
              <i class="material-icons">zoom_in</i>
            </button>
          {/if}
        {/if}
      </div>
    </button>
    {#if +field.settings.extra}
      <button on:click|preventDefault={() => selectOption(option)} tabindex="-1" class="dp_btn option_label">
        {option.label}
        <OptionValue {field} {option}/>
      </button>
    {/if}
  </li>
{/if}

<style lang="scss">li {
  display: flex;
  gap: 4px;
  flex-direction: column;
}
li:hover {
  opacity: 1;
}
li:hover .dp-zoom {
  animation: fadeIn 0.4s ease-in-out 0.3s forwards;
}
li.dp_selected .dp_thumb_btn {
  outline: 3px solid #2fb5d2;
  outline-offset: 3px;
  border-radius: 1px;
}

.dp_thumb {
  position: relative;
}

.dp_thumb_btn {
  padding: 0;
  border-radius: 1px;
  display: flex;
  justify-content: center;
}

.dp-zoom {
  position: absolute;
  right: 3px;
  top: 3px;
  transition: opacity ease-in-out 0.2s;
}
@media (hover: hover) {
  .dp-zoom {
    opacity: 0;
  }
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}</style>
