<script lang="ts">import { recalculate } from "@app/calculator";
import ShortDescription from "@app/Fields/Components/ShortDescription.svelte";
import { input_fields } from "../variables";
export let field;
let entry;
function update(checked) {
    input_fields.updateField(field.name, {
        value: checked ? 1 : 0,
    });
    recalculate($input_fields);
}
</script>

<div class="dp_input_container">
  <div>
    <label class="custom-switch">
      <input type="checkbox" id={`dp_${field.name}`}
             bind:this={entry}
             on:change={() => update(entry.checked)}
             checked={+$input_fields[field.name].value !== 0}
      >
      <span class="slider round"></span>
    </label>
    <label for={`dp_${field.name}`}>{field.label}</label>
    <slot name="tooltip"></slot>
  </div>

  <ShortDescription {field}/>
</div>

<style lang="scss">label {
  cursor: pointer;
}

.custom-switch {
  position: relative;
  display: inline-block;
  width: 34px;
  height: 19px;
}

.custom-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 15px;
  width: 15px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: var(--accent-color);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--accent-color);
}

input:checked + .slider:before {
  transform: translateX(15px);
}

.slider.round {
  border-radius: 15px;
}

.slider.round:before {
  border-radius: 50%;
}</style>
