<script lang="ts">import { recalculate } from "@app/calculator";
import ShortDescription from "@app/Fields/Components/ShortDescription.svelte";
import { spinner } from "@app/utils/spinner";
import { validateField } from "@app/utils/validator";
import { formatMessage } from "@utils/message";
import { dp_adapter, dp_id_module, dp_message, dp_validation, input_fields } from "../variables";
export let field;
const update = (value) => {
    dp_adapter.notifyValueChange(dp_id_module, field.name, value);
    input_fields.updateField(field.name, { value });
    if (validateField(field.name)) {
        recalculate($input_fields);
    }
};
dp_validation[field.name] = () => {
    if (!$input_fields[field.name].visible) {
        return true;
    }
    const value = $input_fields[field.name].value;
    if (!+value && field.settings.required) {
        return formatMessage(dp_message.empty, {
            label: field.label,
        });
    }
    if (+field.settings.max > +field.settings.min) {
        if (value < field.settings.min || value > field.settings.max) {
            return formatMessage(dp_message.min_max, {
                label: field.label,
                min: field.settings.min,
                max: field.settings.max,
            });
        }
    }
    return true;
};
</script>

{#if field.label}
  <label class="attribute_label">
    {field.label}
    {#if field.unit.symbol || field.unit.name}
    <span class="dp_unit_symbol" title="{field.unit.name}">
      ({field.unit.symbol || field.unit.name})
    </span>
    {/if}
  </label>
{/if}

<slot name="tooltip"></slot>

<div class="dp_input_container">
  <input type="text"
         id={`dp_${field.name}`}
         class="form-control"
         value={field.init}
         min={field.settings.min}
         max={field.settings.max}
         use:spinner={{
           update,
           settings: field.settings,
           value: $input_fields[field.name].value
         }}
  />

  <ShortDescription {field}/>

</div>
