import { trigger } from "@utils/utils";

export function enter (node: HTMLElement) {
  node.addEventListener("keyup", (e: KeyboardEvent) => {
    if (e.key === "Enter") {
      trigger(node, "change");
      trigger(node, "submit");
    }
  });
}

export function selectAll (node: HTMLInputElement) {
  node.onclick = () => {
    node.select();
  };
}
