import type { IFieldOption, IFieldSettings } from "@interfaces/field-interface"
import { reorder } from "@utils/reorder"
import { dp_trans } from "@utils/trans-helper"
import { values } from "@utils/utils"
import { dp_ui } from "@views/ts/product/variables"

type Size = {
  width: number;
  height: number;
}

async function getPreferredImageSize (url): Promise<Size> {
  return new Promise(resolve => {
    if (!url) {
      resolve({
        width : 800,
        height: 800,
      })
    }

    let image = new Image()
    image.src = url
    image.onload = function () {
      resolve({ width: image.width, height: image.height })
    }
  })
}

function createSvg (color: string, size: Size): string {
  let svg = `
  <svg xmlns="http://www.w3.org/2000/svg" width="${ size.width }" height="${ size.height }" viewBox="0 0 ${ size.width } ${ size.height }">
    <rect x="0" y="0" width="${ size.width }" height="${ size.height }" fill="${ color }"></rect> 
  </svg>`

  const blob = new Blob([svg], { type: "image/svg+xml" })
  return URL.createObjectURL(blob)
}

export async function openGallery (
  {
    options,
    settings,
    id_current,
    selected,
    selectFn,
  }: {
    options: { [_: number]: IFieldOption },
    settings: IFieldSettings,
    id_current: number,
    selected: number[],
    selectFn: (option: IFieldOption) => void,
  }) {
  dp_ui.setLoading(true)
  const [GLightbox] = await Promise.all([
    import("glightbox"),
    import("glightbox/dist/css/glightbox.css")
  ])
  dp_ui.setLoading(false)

  const ordered = reorder(values(options))

  let slideHTML =
    `<div class="gslide">
        <div class="gslide-inner-content">
            <div class="ginner-container">
                <div class="gslide-media">
                </div>
                <div class="gslide-description">
                    <div class="gdesc-inner">
                        <h4 class="gslide-title"></h4>
                        <button class="btn dp-lightbox-btn">
                          <i class="material-icons">done</i> ${ dp_trans("Select") }
                        </button>
                        <div class="gslide-desc"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>`


  const size = await getPreferredImageSize(ordered.find(op => op.image_url)?.image_url)

  const elements = ordered.map(op => ({
    href  : op.image_url ?? createSvg(op.color, size),
    title : op.label || "&nbsp", // avoid an empty title because the select button wouldn't be displayed
    width : op.image_url ? null : 800,
    height: op.image_url ? null : 800,
    type  : "image",
  }))

  const lightbox = GLightbox.default({
    elements   : elements,
    slideHTML,
    loop       : true,
    openEffect : "fade",
    closeEffect: "fade",
  })

  let index = ordered.findIndex(op => +op.id === +id_current)
  lightbox.openAt(index)

  selected.forEach(id => {
    let idx = ordered.findIndex(op => +op.id === +id)
    jQuery(lightbox.modal).find(".dp-lightbox-btn").eq(idx).addClass("btn-success")
  })

  jQuery(lightbox.modal).on("click touchend", ".dp-lightbox-btn", (ev) => {
    const index = lightbox.index
    selectFn(ordered[index])
    if (settings.multiselect) {
      $(ev.currentTarget).toggleClass("btn-success")
    } else {
      $(".dp-lightbox-btn").removeClass("btn-success")
      $(ev.currentTarget).addClass("btn-success")
    }
    return false
  })
}
