import type { ICalculatorResult } from "@interfaces/calculator-interface";
import type { IInputField, IInputFields } from "@interfaces/input-field-interface";
import { debounce } from "@utils/debounce";
import { updateDiscountsTable } from "@utils/discounts-table";
import { optimizeFields } from "@views/ts/product/utils/fields";
import { getProductData } from "@views/ts/product/utils/product";
import { dp, dp_adapter, dp_calc, dp_id_module, dp_post, dp_ui, input_fields } from "@views/ts/product/variables";
import { get } from "svelte/store";

export function copyQuantityFromData (input_fields) {
  const qty = jQuery("#quantity_wanted").val() as string;
  if (!input_fields["quantity"]) {
    input_fields["quantity"] = {
      name : "quantity",
      value: 1,
    };
  }
  input_fields["quantity"].value = +qty;
}

export function setQuantityInputValue (quantity_input_field: IInputField) {
  jQuery("#quantity_wanted").val(quantity_input_field.value);
}

export function updateQuantity () {
  const qty = jQuery("#quantity_wanted").val() as string;

  let input_fields_data = get(input_fields);
  if (input_fields_data["quantity"] && input_fields_data["quantity"].value != qty) {
    input_fields.updateField("quantity", {
      name : "quantity",
      value: qty,
    });
  }
}

let calculationPromises = [];

export function abortCalculations () {
  calculationPromises.map((request) => {
    dp_adapter.release("price");
    request.abort();
  });
  calculationPromises = [];
}

export async function calculate (input_fields_data: IInputFields) {
  dp_ui.setLoading(true);

  abortCalculations();

  const optimized_fields = optimizeFields(input_fields_data);
  await dp_adapter.lockRelease("price");

  dp_adapter.lock("price");

  const data = {
    action      : "calculate_result",
    fields      : optimized_fields,
    adapter_data: dp_adapter.getData(),
    ...getProductData(),
  };

  const calculationPromise = dp_post.post(dp.controllers.calculator, data);
  calculationPromises.push(calculationPromise);
  let res: ICalculatorResult = null;
  try {
    res = await calculationPromise;
  } catch (e) {
    dp_ui.setLoading(false);
    dp_adapter.release("price");
    return;
  }
  dp_ui.setLoading(false);
  if (res && typeof res === "object") {
    if (+res.success) {
      dp.error = null;
      dp_calc.updateData(res);
      dp_ui.setOOS(res.oos);
      input_fields.updateData(res.input_fields);
      dp_adapter.setModulePrices(dp_id_module, res.customization_prices);
      dp_adapter.setModuleResponse(dp_id_module, res);
      updateDiscountsTable(res.quantity_discounts, res.use_tax);
    } else {
      console.error(res.message);
    }
  }
  calculationPromises = [];
  dp_adapter.release("price");
}

export const recalculate: (input_fields_data: IInputFields) => void = debounce(calculate, 333);
