import SliderOptions = JQueryUI.SliderOptions
import type { IFieldSettings } from "@interfaces/field-interface"
import { trigger } from "@utils/utils"

export function slider (node, params) {
  const update = typeof params === "function" ? params : params.update
  const settings: IFieldSettings = params.settings
  const jqNode = jQuery(node)

  const sliderOptions: SliderOptions = {
    min  : +settings.min,
    step : settings.step ?? 1,
    max  : 100,
    value: params.value,
    slide: (_, ui) => update(ui.value),
    stop : (_, ui) => update(ui.value),
  }

  if (+settings.max > +settings.min) {
    sliderOptions.max = +settings.max
  }
  jqNode.slider(sliderOptions)

  return {
    update (params) {
      let value = params.value
      let min = params.settings.min
      let max = params.settings.max > params.settings.min ?
        params.settings.max :
        sliderOptions.max
      if (value < min) {
        value = min
      }
      if (value > max) {
        value = max
      }
      if (typeof jqNode.slider === "function") {
        jqNode.slider("value", value)
        jqNode.slider("option", "min", min)
        jqNode.slider("option", "max", max)
        trigger(node, "update", {
          value,
          min,
          max
        })
      }
    }
  }
}
