import { writableStore } from "@utils/vegemite";
import type { UiStore, UiStoreData } from "@views/ts/product/types/UiStore";
import { namespaced, Topics } from "@views/ts/product/utils/topics";
import { get } from "svelte/store";

export function createUiStore<T> (data: UiStoreData): UiStore {
  const vegStore = writableStore(data);

  const { update, dispatch } = vegStore;

  const store: UiStore = {
    ...vegStore,

    setLoading (loading) {
      return update((state) => {
        state.loading = loading;
        return state;
      });
    },

    isTooltipEmpty (id_field: number) {
      const data = get(store);
      return data.tooltips?.[id_field]?.empty ?? true;
    },

    setTooltipEmpty (field, empty) {
      return dispatch(namespaced(Topics.TOOLTIP_UPDATE, field.name), (state) => {
        state.tooltips[field.id] = { ...state.tooltips[field.id], ...{ empty } };
        return state;
      });
    },

    setOOS (oos) {
      return update((state) => {
        state.oos = oos;
        return state;
      });
    },
  };
  return store;
}
