<script lang="ts">import { isStepHidden } from "@app/utils/visibility";
import { dp, dp_calc, dp_step } from "@app/variables";
import { reorder } from "@utils/reorder";
import { dp_trans } from "@utils/trans-helper";
let steps = reorder(dp.steps);
function selectStep(step) {
    dp_step.setStep(step);
}
</script>

<div class="dp_steps_buttons">
  {#if $dp_step.prev_step && !isStepHidden($dp_calc.visibility, $dp_step.prev_step.id)}
    <button class="btn btn-info dp_step_prev" on:click|preventDefault={() => selectStep($dp_step.prev_step)}>
      <i class="material-icons">chevron_left</i> {dp_trans('Back')}
    </button>
  {/if}
  <span class="dp_spacer"></span>
  {#if $dp_step.next_step && !isStepHidden($dp_calc.visibility, $dp_step.next_step.id)}
    <button class="btn btn-info dp_step_next" on:click|preventDefault={() => selectStep($dp_step.next_step)}>
      {dp_trans('Next')} <i class="material-icons">chevron_right</i>
    </button>
  {/if}
</div>

<style>
  .dp_spacer {
    flex-grow: 1;
  }

</style>
