<script lang="ts">import { fade } from "svelte/transition";
import { dp_ui } from "../variables";
$: $dp_ui.success_message && setTimeout(() => {
    $dp_ui.success_message = null;
}, 3000);
$: $dp_ui.error_message && setTimeout(() => {
    $dp_ui.error_message = null;
}, 3000);
$: $dp_ui.blur_ui = (!!$dp_ui.success_message || !!$dp_ui.success_message);
</script>

<div style="position: absolute; bottom: 0; left: 0; right: 0;">
  {#if $dp_ui.error_message}
    <div class="alert alert-danger tn_invalid" transition:fade>
      {$dp_ui.error_message}
    </div>
  {/if}

  {#if $dp_ui.success_message}
    <div class="alert alert-success" transition:fade>
      {$dp_ui.success_message}
    </div>
  {/if}
</div>
