import { recalculate } from "@app/calculator"
import { getFieldGroup } from "@app/utils/group"
import { isNumeric } from "@app/utils/math"
import type { IFieldOption, IFieldSettings } from "@interfaces/field-interface"
import type { IInputField, IInputFields } from "@interfaces/input-field-interface"
import { forEach } from "@utils/foreach"
import { cloneDeep, values } from "@utils/utils"
import { List } from "@views/ts/interfaces/Generics"
import { replaceAll } from "@views/ts/product/utils/strings"
import { namespaced, Topics } from "@views/ts/product/utils/topics"
import { validateFields } from "@views/ts/product/utils/validator"
import { dp, dp_step, dp_ui, fields, input_fields } from "@views/ts/product/variables"
import * as escapeHtml from "escape-html"
import { get } from "svelte/store"

export type UpdateFieldOptions = {
  recalculate?: boolean;
}

export function addHelpers () {
  window.getField = function (name) {
    return fields.getField(name)
  }
  window.updateField = function (name, data: IFieldSettings, options: UpdateFieldOptions = {}) {
    const defaultOptions: UpdateFieldOptions = {
      recalculate: true
    }
    const updateFieldOptions = { ...defaultOptions, ...options }

    fields.updateField(name, { settings: data })
    input_fields.updateField(name, data)
    input_fields.updateField("changed", { value: name })
    setTimeout(() => {
      dp_ui.dispatch(namespaced(Topics.FIELD_UPDATE, name))
    }, 0)

    if (updateFieldOptions.recalculate) {
      recalculate(get(input_fields))
    }
  }

  window.validateFields = validateFields
}

export function optimizeFields (input_fields) {
  const remove_keys = ["force_id", "id", "position", "image_url", "thumb_url", "dir", "ext", "thumb_suffix", "value_formatted", "label", "field"]
  const input_fields_data = cloneDeep(input_fields)
  forEach(input_fields_data, (input_field) => {
    remove_keys.map(key => delete input_field[key])
  })
  return input_fields_data
}

export function execCustomFunction (name: string, input_field: IInputField) {
  const func_name = `dp_${ name }`
  if (typeof window[func_name] === "function") {
    (window[func_name] as Function).call(null, input_field.value, input_field.selected_options)
  }
}

export function replaceFieldValues (content: string, input_fields: IInputFields) {
  let fields = values(input_fields)
  return replaceAll(
    content,
    fields.map(field => `[${ field.name }]`),
    fields.map(field => escapeHtml(field.value_formatted))
  )
}

export function getOptionsTotal (
  options: List<IFieldOption>,
  selected_options: number[],
  multiselect = false,
) {
  const values = selected_options.map(id => options[id]?.value)
  const hasStrings = values.map(val => isNumeric(val)).includes(false)
  if (hasStrings) {
    if (values.length === 1 && !multiselect) {
      return values[0]
    }
    return values.join(";") + ";"
  }
  return values.reduce((total, value) => total += parseFloat(value.toString()), 0)
}

export function goToField (field_name) {
  let field = fields.getField(field_name)
  const id_step = field.id_group === 0 ? field.id_step : getFieldGroup(field.id_group)?.id_step
  if (id_step) {
    dp_step.setStep(values(dp.steps).find(s => s.id === id_step))
  }
}
