import type { IInputField } from "@interfaces/input-field-interface";
import { abortCalculations } from "@views/ts/product/calculator";
import type { InputFieldsStore } from "@views/ts/product/types/InputFieldsStore";
import { execCustomFunction } from "@views/ts/product/utils/fields";
import { get, writable } from "svelte/store";

export function createInputFieldsStore<IInputFields> (input_fields): InputFieldsStore {
  const { set, update, subscribe } = writable(input_fields);

  const store = {
    subscribe,
    update,
    set,

    updateField: (name: string, input_field: IInputField) => {
      return update((state) => {
        state[name] = { ...state[name], ...input_field };
        state["changed"].value = name;
        abortCalculations();
        execCustomFunction(name, input_field);
        return state;
      });
    },

    updateData: (data) => {
      set(data);
    },

    getData: () => get(store)
  };
  return (store);
}
