import { writableStore } from "@utils/vegemite";

export function createValidationStore<ValidationStore> (data) {
  const store = writableStore(data);

  const { update } = store;

  return ({
    ...store,
    setError (field_name: string, error: string) {
      return update((state) => {
        state[field_name] = error;
        return state;
      });
    },
    resetError (field_name: string) {
      return update((state) => {
        delete state[field_name];
        return state;
      });
    },
  });
}
