<script lang="ts">import { namespaced, Topics } from "@app/utils/topics";
import { dp_ui } from "../variables";
export let field;
dp_ui.on(namespaced(Topics.FIELD_UPDATE, field.name), () => {
    dp_ui.setTooltipEmpty(field, !field.description && !field.image_url);
});
</script>

<label>{field.label}</label>

{#if field.image_url}
  <div>
    <img width="300" src="{field.image_url}" alt="Preview">
  </div>
{/if}

<p>
  {@html field.description}
</p>

