import type { IPostHelper } from "@interfaces/post-helper";
import { dp, dp_message } from "@views/ts/product/variables";
import Timeout = NodeJS.Timeout;

declare const showSuccessMessage: Function;
declare const showErrorMessage: Function;

export class PostHelper implements IPostHelper {

  private timeouts = new Map<string, number>();

  public throttle (context: string, delay: number): Promise<void> {
    let id = window.setTimeout(() => {
      Promise.resolve();
    });
    this.timeouts.set(context, id);
    return;
  }

  public post (url: string, data: any): any {
    data.ajax = true;
    data.id_product = dp.id_product;
    return jQuery.post({
      url,
      data,
      dataType: "json",
      success : function (response) {
        if (!data.no_msg) {
          if (response.success) {
            if (typeof showSuccessMessage === "function") {
              showSuccessMessage(dp_message.success);
            }
          } else if (!data.no_error_msg) {
            if (typeof showErrorMessage === "function") {
              showErrorMessage(response.message);
            }
          }
        }
      }
    });
  }
}
