<script lang="ts">import { dp_trans } from "@utils/trans-helper";
import { namespaced, Topics } from "@app/utils/topics";
import { dp_ui } from "../variables";
export let field;
let has_min_max = false;
let has_image = false;
dp_ui.on(namespaced(Topics.FIELD_UPDATE, field.name), () => {
    has_min_max = +field.settings.max > +field.settings.min;
    has_image = !!field.image_url;
    dp_ui.setTooltipEmpty(field, !field.description && !has_min_max && !has_image);
});
</script>

<label>{field.label}</label>

{#if field.image_url}
  <div>
    <img width="300" src="{field.image_url}" alt="Preview">
  </div>
{/if}

{#if has_min_max}
  <div>
    <strong>{dp_trans('Min length')}:</strong> {field.settings.min} {dp_trans('characters')}
  </div>
  <div>
    <strong>{dp_trans('Max length')}:</strong> {field.settings.max} {dp_trans('characters')}
  </div>
{/if}

<p>
  {@html field.description}
</p>
