<script lang="ts">import { dp_trans } from "@utils/trans-helper";
import { namespaced, Topics } from "@app/utils/topics";
import { dp_ui } from "../variables";
export let field;
let has_min_width = false;
let has_min_height = false;
let has_max_size = false;
let has_image = false;
dp_ui.on(namespaced(Topics.FIELD_UPDATE, field.name), () => {
    has_min_width = field.settings.min_width > 0;
    has_min_height = field.settings.min_height > 0;
    has_max_size = field.settings.max_size > 0;
    has_image = !!field.image_url;
    dp_ui.setTooltipEmpty(field, !field.description && !has_min_width && !has_min_height && !has_max_size && !has_image);
});
</script>

<label>{field.label}</label>

{#if field.image_url}
  <div>
    <img width="300" src="{field.image_url}" alt="Preview">
  </div>
{/if}

{#if has_min_width}
  <div>
    <strong>{dp_trans('Min width')}:</strong> {field.settings.min_width} {dp_trans('pixels')}
  </div>
{/if}

{#if has_min_height}
  <div>
    <strong>{dp_trans('Min height')}:</strong> {field.settings.min_height} {dp_trans('pixels')}
  </div>
{/if}

{#if has_max_size}
  <div>
    <strong>{dp_trans('Max size')}:</strong> {field.settings.max_size} {dp_trans('MB')}
  </div>
{/if}

<p>
  {@html field.description}
</p>
