import { dp } from "@views/ts/product/variables";

declare const dp_id_input: number;

export function getProductData () {
  return {
    id_product  : dp.id_product,
    id_attribute: dp.id_attribute,
    attributes  : getAttributes(),
    quantity    : +$("#quantity_wanted").val(),
    hash        : location.hash,
    dp_id_input : dp_id_input,
    dp_cart     : dp.id_cart,
    dp_customer : dp.id_customer,
  };
}

function getAttributes () {
  const form = $("form#add-to-cart-or-refresh");
  if (form.length) {
    const attributes = {};
    const data = form.serializeArray();
    data.forEach((obj: { name, value }) => {
      if (/^group\[/gi.test(obj.name)) {
        attributes[obj.name] = obj.value;
      }
    });
    return attributes;
  }
  return null;
}
