<script>
  import { createEventDispatcher, onMount } from "svelte"
  import { dp_ui } from "@views/ts/product/variables"
  import "@plugins/form/jquery.form"

  export let action
  export let data = {}
  export let percent = 0

  let form

  let dispatch = createEventDispatcher()

  onMount(() => {
    jQuery(form).ajaxForm({
      dataType: "json",
      data,
      beforeSubmit: () => {
        $dp_ui.loading = true
        percent = 0
      },
      uploadProgress: (event, position, total, percentage) => {
        if (typeof percentage === "number") {
          percent = percentage
        }
      },
      success: (response) => {
        $dp_ui.loading = false
        percent = 100
        dispatch("success", {
          response
        })
      },
    })
  })

</script>

<form bind:this={form} {action} method="post" enctype="multipart/form-data">
  <slot/>
</form>
