<script lang="ts">import { replaceFieldValues } from "@app/utils/fields";
import { input_fields } from "../variables";
export let field;
$: value = field.name && $input_fields[field.name].value;
$: content = replaceFieldValues(value || field.description, $input_fields);
</script>

<div class="dp_input_container">
  {#if field.label}
    <label class="attribute_label">{field.label}:</label>
  {/if}
  <span class="dp_html_content">{@html content}</span>
</div>
