<script lang="ts">import { isStepHidden } from "@app/utils/visibility";
import { dp_calc, dp_step } from "@app/variables";
import { dp_trans } from "@utils/trans-helper";
export let step;
$: disabled = isStepHidden($dp_calc.visibility, step.id) ||
    step.position - $dp_step.current_step.position > 1;
function selectStep(step) {
    dp_step.setStep(step);
}
</script>

<li class="dp_step_label" class:active={$dp_step.current_step && $dp_step.current_step.id === step.id}>
  <button on:click|preventDefault={() => selectStep(step)}
          class="btn btn-info"
          {disabled}>
    {step.step.label || dp_trans("Unknown step")}
  </button>
</li>
