export function moveContainer (container) {
  const jqContainer = jQuery(container);
  let dp_hook = "#add-to-cart-or-refresh .product-variants";
  let customHook = (window as any).dp_hook;
  if (customHook) {
    dp_hook = customHook;
  }
  let hook = $(dp_hook);
  if (!hook.length) {
    console.warn("Dynamic Product: could not move module container, hook not found!");
  }
  customHook ? jqContainer.appendTo(dp_hook) : jqContainer.insertAfter(dp_hook);
}

export function updateBodyClasses (classes: string[]) {
  try {
    const body: HTMLElement = document.body;

    const previousClasses: string[] = (body as any).dp_classes;
    if (previousClasses) {
      previousClasses.map(cls => body.classList.remove(cls));
    }

    if (classes) {
      const class_names = classes.map(cls => "dp_condition_" + cls);
      (body as any).dp_classes = class_names;
      class_names.map(cls => body.classList.add(cls));
    }
  } catch (e) {

  }
}

export function stopEvent (event: Event | JQuery.Event) {
  if (!event) {
    return false;
  }
  event.preventDefault();
  event.stopPropagation();
}
