<script lang="ts">import { dp_trans } from "@utils/trans-helper";
import { namespaced, Topics } from "@app/utils/topics";
import { dp_ui } from "../variables";
export let field;
dp_ui.setTooltipEmpty(field, false);
let has_max_size = false;
dp_ui.on(namespaced(Topics.FIELD_UPDATE, field.name), () => {
    has_max_size = field.settings.max_size > 0;
});
</script>


<label>{field.label}</label>

{#if field.image_url}
  <div>
    <img width="300" src="{field.image_url}" alt="Preview">
  </div>
{/if}

<div>
  <strong>{dp_trans('Accepted file types')}:</strong> {field.settings.extensions}
</div>

{#if has_max_size}
  <div>
    <strong>{dp_trans('Max size')}:</strong> {field.settings.max_size} {dp_trans('MB')}
  </div>
{/if}

<p>
  {@html field.description}
</p>
