import type { StepsStore } from "@app/stores/steps-store"
import { createStepsStore } from "@app/stores/steps-store"
import { UpdateFieldOptions } from "@app/utils/fields"
import type { IField } from "@interfaces/field-interface"
import type { IInputFields } from "@interfaces/input-field-interface"
import type { IMessages } from "@interfaces/messages"
import type { IPostHelper } from "@interfaces/post-helper"
import { PostHelper } from "@utils/post-helper"
import { copyQuantityFromData, setQuantityInputValue } from "@views/ts/product/calculator"
import { createCalculatorStore } from "@views/ts/product/stores/calc-store"
import { createFieldsStore } from "@views/ts/product/stores/fields-store"
import { createInputFieldsStore } from "@views/ts/product/stores/input-fields-store"
import { createUiStore } from "@views/ts/product/stores/ui-store"
import { createValidationStore } from "@views/ts/product/stores/validation-store"
import type { CalculatorStore } from "@views/ts/product/types/CalculatorStore"
import type { IDynamicProduct } from "@views/ts/product/types/DynamicProduct"
import type { InputFieldsStore } from "@views/ts/product/types/InputFieldsStore"
import type { UiStore } from "@views/ts/product/types/UiStore"
import type { ValidationStore } from "@views/ts/product/types/ValidationStore"
import { Adapter } from "../adapter/Adapter"

export const dp_id_module: number = window.dp_id_module

export const prestashop: any = window.prestashop

export const dp_input: IInputFields = window.dp_input

const adapter = window.tn_adapter || new Adapter()
adapter.registerModule(dp_id_module, {
  customization_callback: null,
  sync_callback         : null,
  response_callback     : null,
})
export let dp_adapter: Adapter = adapter
window.tn_adapter = window.tn_adapter || dp_adapter

export let dp: IDynamicProduct = window.dp

export let dp_message: IMessages = window.dp_message

export let dp_ui: UiStore = createUiStore({
  loading        : false,
  success_message: null,
  error_message  : null,
  blur_ui        : false,
  force_display  : false,
  tooltips       : {},
  oos            : false,
})

export let dp_step: StepsStore = createStepsStore(dp.steps)

export let fields = createFieldsStore(dp.grouped_fields)

let dp_input_fields = dp.calculation.input_fields
if (dp_input) {
  dp_input_fields = dp_input
  setQuantityInputValue(dp_input_fields["quantity"])
} else {
  copyQuantityFromData(dp_input_fields)
}

export let input_fields: InputFieldsStore = createInputFieldsStore(dp_input_fields)
window.dp_fields = input_fields

export let dp_calc: CalculatorStore = createCalculatorStore({
  visibility: dp.calculation.visibility
})
window.dp_calc = dp_calc

export let dp_val: ValidationStore = createValidationStore({})

export let dp_validation: { [field_name: string]: () => boolean | string } = {}

export let dp_post: IPostHelper = new PostHelper()

declare global {
  interface Window {
    dp: IDynamicProduct;

    dp_message: IMessages;

    dp_id_module: number;

    tn_adapter: Adapter;

    prestashop: any;

    dp_input: IInputFields;

    dp_cart_btn_selector: string;
    dp_product_image_selector: string;

    dp_calc: CalculatorStore;
    dp_fields: InputFieldsStore;

    dp_hot_mode: boolean;
    dp_hot_mode_port: string;

    dp_discounts_td_index: number;
    dp_discounts_display_type: "totals" | "unit_prices" | "savings" | "disabled";

    dpSaveCustomization (add_to_cart: boolean): Promise<any>;

    getField (name: string): IField;

    updateField (name: string, data: Partial<IField>, options: UpdateFieldOptions);

    validateFields ();
  }
}
