import type { IGroupedFields } from "@interfaces/field-group-interface"
import type { IField } from "@interfaces/field-interface"
import type { List } from "@interfaces/Generics"
import { forEach } from "@utils/foreach"
import { values } from "@utils/utils"
import * as deepmerge from "deepmerge"
import { get, writable } from "svelte/store"
import type { FieldsStore } from "../types/FieldsStore"

export function createFieldsStore<type> (grouped_fields: List<IGroupedFields>): FieldsStore {
  const { set, update, subscribe } = writable(grouped_fields)

  const store = {
    subscribe,
    update,
    set,

    getField: (name: string): IField => {
      let field = null
      const grouped_fields: List<IGroupedFields> = get(store)
      forEach(grouped_fields, (field_group) => {
        let found = values(field_group.fields).find(f => f.name === name)
        if (found) {
          field = found
          return
        }
      })
      return field
    },

    updateField: (name: string, data: Partial<IField>) => {
      return update((grouped_fields) => {
        forEach(grouped_fields, grouped_field => {
          let field = values(grouped_field.fields).find(f => f.name === name)
          if (field) {
            grouped_fields[grouped_field.id].fields[field.id] = deepmerge(field, data)
          }
        })
        return grouped_fields
      })
    },
  }
  return (store)
}
