import type { List } from "@interfaces/Generics";
import type { IProductStep } from "@ProductConfig/types/StepsStore";
import { reorder } from "@utils/reorder";
import type { Writable } from "svelte/store";
import { writable } from "svelte/store";

export type StepsStoreData = {
  prev_step: IProductStep;
  current_step: IProductStep;
  next_step: IProductStep;
}

export interface StepsStore extends Writable<StepsStoreData> {
  setStep (step: IProductStep);
}

export function createStepsStore (steps: List<IProductStep>): StepsStore {
  const ordered_steps = reorder(steps);
  const store = writable({
    prev_step   : null,
    current_step: ordered_steps[0],
    next_step   : ordered_steps[1],
  });

  return {
    ...store,

    setStep: (step: IProductStep) => {
      store.set({
        prev_step   : ordered_steps.find(s => s.position === step.position - 1),
        current_step: step,
        next_step   : ordered_steps.find(s => s.position === step.position + 1),
      });
    }
  };
}
