import { writableStore } from "@utils/vegemite";
import type { CalculatorStore } from "@views/ts/product/types/CalculatorStore";

export enum CalcTopics {
  DATA_UPDATED
}

export function createCalculatorStore<ICalculatorResult> (data): CalculatorStore {
  const store = writableStore(data);

  let { dispatch } = store;

  return {
    ...store,
    updateData (data) {
      return dispatch(CalcTopics.DATA_UPDATED, (state) => {
        state = { ...state, ...data };
        return state;
      });
    }
  };
}
