<script lang="ts">import { recalculate } from "@app/calculator";
import ShortDescription from "@app/Fields/Components/ShortDescription.svelte";
import { input_fields } from "../variables";
export let field;
let entry;
function update(checked) {
    input_fields.updateField(field.name, {
        value: checked ? 1 : 0,
    });
    recalculate($input_fields);
}
</script>

<div class="dp_input_container">
  <div class="dp-checkbox-container">
    <label for={`dp_${field.name}`}>
      <input type="checkbox" id={`dp_${field.name}`}
             bind:this={entry}
             on:change={() => update(entry.checked)}
             checked={+$input_fields[field.name].value !== 0}
      >
      {field.label}
    </label>
    <span>
    <slot name="tooltip"></slot>
  </span>
  </div>

  <ShortDescription {field}/>

</div>

<style>
  label {
    display: inline-flex;
    align-items: center;
    gap: .3em;
  }

  .dp-checkbox-container {
    display: flex;
    align-items: flex-start;
    gap: .3em;
  }

</style>
