import type { ICartUpdateData } from "@interfaces/cart-data";
import { dp_post, prestashop } from "@views/ts/product/variables";

async function refreshCart (data: ICartUpdateData) {
  let form = $("#add-to-cart-or-refresh");

  const form_data = Object.fromEntries(form.serializeArray().map(({ name, value }) => [name, value]));

  const cart_url = form.attr("action");
  const resp = await dp_post.post(cart_url, {
    ...form_data,
    action              : "update",
    id_product          : data.id_product,
    id_product_attribute: data.id_product_attribute,
    id_customization    : data.id_customization,
  });

  prestashop.emit("updateCart", {
    reason: {
      linkAction        : "add-to-cart",
      idProduct         : resp.id_product,
      idProductAttribute: resp.id_product_attribute,
      idCustomization   : resp.id_customization,
    },
    resp,
  });
}

export { refreshCart };
