import { forEach } from "@utils/foreach";

export function getConvertedValues (field_name: string, value: number) {
  const converted_values = {};

  const suffixes = {
    ""   : 1,
    "_mm": 0.1,
    "_m" : 100
  };

  const names = ["width", "height"];

  forEach(names, (name) => {
    forEach(suffixes, (coeff: number, suffix) => {
      if (field_name === `${ name }${ suffix }`) {
        forEach(suffixes, (suffix_coeff: number, suffix) => {
          converted_values[`${ name }${ suffix }`] = coeff / suffix_coeff * value;
        });
      }
    });
  });

  return converted_values;
}
