<script lang="ts">import { recalculate } from "@app/calculator";
import ShortDescription from "@app/Fields/Components/ShortDescription.svelte";
import { validateField } from "@app/utils/validator";
import { formatMessage } from "@utils/message";
import { dp_message, dp_validation, input_fields } from "../variables";
export let field;
const update = (value) => {
    input_fields.updateField(field.name, { value });
    if (validateField(field.name)) {
        recalculate($input_fields);
    }
};
dp_validation[field.name] = () => {
    if (!$input_fields[field.name].visible) {
        return true;
    }
    const value = $input_fields[field.name].value;
    if (field.settings.required && value === "") {
        return formatMessage(dp_message.empty, {
            label: field.label,
        });
    }
    if (field.settings.max > field.settings.min) {
        if (value.length < field.settings.min) {
            return formatMessage(dp_message.short, {
                label: field.label,
                min: field.settings.min,
            });
        }
        if (value.length > field.settings.max) {
            return formatMessage(dp_message.long, {
                label: field.label,
                max: field.settings.max,
            });
        }
    }
    return true;
};
$: store_value = ($input_fields[field.name].value || "");
$: value = store_value;
</script>

{#if field.label}
  <label class="attribute_label">
    {field.label}
  </label>
{/if}

<slot name="tooltip"></slot>

<div class="dp_input_container">
  <div style="position: relative;">
    <textarea class="form-control"
              rows="4"
              id={`dp_${field.name}`}
              on:change={() => update(value)}
              bind:value
    />
    {#if field.settings.max}
      <span class="length" class:error={value.length > field.settings.max}>
        {value.length} / {field.settings.max}
      </span>
    {/if}
  </div>

  <ShortDescription {field}/>

</div>

<style lang="scss">.length {
  font-size: 13px;
  position: absolute;
  color: #AAA;
  right: 2ch;
  bottom: 0;
}
.length.error {
  color: red;
}</style>
