<script lang="ts">import { recalculate } from "@app/calculator";
import ShortDescription from "@app/Fields/Components/ShortDescription.svelte";
import { button } from "@app/utils/button";
import { slider } from "@app/utils/slider";
import { enter } from "@utils/actions";
import { formatMessage } from "@utils/message";
import { dp_message, dp_validation, input_fields } from "../variables";
export let field;
let input;
$: min = field.settings.min | 0;
$: max = field.settings.max > field.settings.min ? field.settings.max : 100;
$: step = Number(field.settings.step);
$: {
    if (isNaN(step) || !step) {
        step = 1;
    }
}
function update(value) {
    inputValue = value;
    input_fields.updateField(field.name, { value });
    recalculate($input_fields);
}
function increment() {
    update(Math.min(+$input_fields[field.name].value + step, max));
    inputValue = $input_fields[field.name].value;
}
function decrement() {
    update(Math.max(+$input_fields[field.name].value - step, min));
    inputValue = $input_fields[field.name].value;
}
let textWidth = 0;
$: sliderValue = $input_fields[field.name].value;
$: inputValue = sliderValue;
function updateSliderValue() {
    inputValue = +inputValue;
    if (inputValue < min) {
        inputValue = min;
    }
    if (inputValue > max) {
        inputValue = max;
    }
    update(inputValue);
}
function handleSliderUpdate(ev) {
    inputValue = ev.detail.value;
}
dp_validation[field.name] = () => {
    if (!$input_fields[field.name].visible) {
        return true;
    }
    const value = $input_fields[field.name].value;
    if (+field.settings.max > +field.settings.min) {
        if (value < field.settings.min || value > field.settings.max) {
            return formatMessage(dp_message.min_max, {
                label: field.label,
                min: field.settings.min,
                max: field.settings.max,
            });
        }
    }
    return true;
};
</script>

<div>
  <label class="attribute_label" style="display: inline-block;">
    {field.label}&nbsp;
    <span style="font-weight: normal; display: inline-flex;">
      (
      <input
        type="number"
        class="value-input"
        bind:this={input}
        bind:value={inputValue}
        on:change={updateSliderValue}
        use:enter
        on:click={() => input.select()}
        style="width: {textWidth}px"
        min={field.settings.min}
        data-cy="value"
        {max}
      >
      {#if field.unit.name || field.unit.symbol}&nbsp;{field.unit.symbol ? field.unit.symbol : field.unit.name}{/if}
      )
    </span>
  </label>

  <slot name="tooltip"></slot>

  {#key (inputValue || '').toString().length}
    <span
      bind:clientWidth={textWidth}
      style="display: inline-block;"
      class="dp_away"
    >{inputValue}</span>
  {/key}

</div>

<div class="dp_input_container">
  <button use:button class="ui-button ui-widget" data-cy="down"
          on:click|preventDefault={decrement}
  >
    <span class="ui-icon ui-icon-minus"></span>
  </button>
  <div class="dp_slider">
    <div id={`dp_${field.name}`}
         on:update={handleSliderUpdate}
         use:slider={{
         update,
         settings: field.settings,
         value: $input_fields[field.name].value
       }}
    ></div>
  </div>
  <button use:button class="ui-button ui-widget" data-cy="up"
          on:click|preventDefault={increment}
  >
    <span class="ui-icon ui-icon-plus"></span>
  </button>

  <ShortDescription {field}/>

</div>


<style lang="scss">.dp_slider {
  position: relative;
  top: 4px;
  display: inline-block;
  width: 250px;
  margin: 0 10px;
}

.value-input {
  background-color: transparent;
  border: none;
  padding: 0px 8px;
  box-sizing: content-box;
  text-align: center;
}</style>
