import { optimizeFields } from "@views/ts/product/utils/fields";
import { getProductData } from "@views/ts/product/utils/product";
import { dp, dp_adapter, dp_post, input_fields } from "@views/ts/product/variables";
import { get } from "svelte/store";

export async function saveCustomization (action, add_to_cart = true) {
  const input_fields_data = optimizeFields(get(input_fields));

  let form = $("#add-to-cart-or-refresh");

  const form_data = Object.fromEntries(form.serializeArray().map(({ name, value }) => [name, value]));

  const data = {
    ...form_data,
    action     : action,
    fields     : input_fields_data,
    add_to_cart: +add_to_cart,
    ...getProductData(),
  };

  dp_adapter.addCustomizationData(data);

  return await dp_post.post(dp.controllers.customization, data);
}
