export function infoTooltip (node?: HTMLElement, params?) {
  let content = ""
  if (params?.element) {
    content = params.element.innerHTML
  } else {
    if (node) {
      content = node.title
      node.title = ""
    }
  }

  if (content) {
    let $node = jQuery(node)
    if ($node.data("ui-tooltip")) {
      $node.tooltip("destroy")
    }
    $node.attr("title", "").tooltip({
      content: content,
      // @ts-ignore
      title       : content,
      html        : true,
      hide        : false,
      tooltipClass: "dp-tooltip",
      classes     : {
        "ui-tooltip": "dp-tooltip"
      },
    })
  }
}
