<script lang="ts">import { recalculate } from "@app/calculator";
import ShortDescription from "@app/Fields/Components/ShortDescription.svelte";
import { picker } from "@utils/front-picker";
import { dp_trans } from "@utils/trans-helper";
import { input_fields } from "../variables";
export let field;
function update(ev) {
    input_fields.updateField(field.name, { value: ev.detail.color });
    recalculate($input_fields);
}
</script>

<label class="attribute_label">
  {field.label}
</label>

<slot name="tooltip"></slot>

<div class="dp_input_container">
  <div class="picker-div">

    <input class="form-control"
           id={`dp_${field.name}`}
           bind:value={$input_fields[field.name].value}
           on:change={() => recalculate($input_fields)}
           title="{dp_trans('Pick a custom color')}"
    />

    <span class="dp_btn"
          data-cy="open"
          use:picker={{color: $input_fields[field.name].value}}
          on:change={update}
          on:changestop={update}
          on:swatchselect={update}
          style="color: {$input_fields[field.name].value}"
    >
      <i class="material-icons">palette</i>
    </span>

  </div>

  <ShortDescription {field}/>

</div>

<style lang="scss">.picker-div {
  display: inline-block;
  position: relative;
}

span {
  display: flex;
  align-items: center;
  position: absolute;
  height: 100%;
